.myname{
    color: #E0F3FF;
    text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.5);
    font-family: Georgia;
}
.profile-pic{
  margin-left: -70px;
  margin-top: -80px;
  height: 350px;
}
.profile-frame{
  display: inline-block;
  position: relative;
  width: 150px;
  height: 150px;
  overflow: hidden;
  border-radius: 50%;
  box-shadow: 2px 3px 3px rgb(59, 57, 57, 0.5);
}

.aboutme{
    color: #3f3d46c0;
    padding-left: 20%;
    padding-right: 20%;
    text-shadow: .5px .5px 0px rgba(0,0,0,0.5);
}

.carousel{
    color: #ffc962;
}
.links{
    display: flex;
    flex-direction: column;
    text-align: center;
    width: 40%;
    margin: auto;
}
.row{
    justify-content: center;
}
.icon{ 
    margin: 2vmin 3vmin 1vmin 3vmin;
    color: rgb(40, 80, 78);
    transition: margin .2s;
}
.icon:hover{
    margin-top: 1vmin;
}

.arrow{
    border: none;
    background: none;
    color: white;
    font-size: 1vmin;
    transition: color 0.2s;
    position: absolute;
    bottom: 2%;
}

.arrow:hover{
    color: black;
    text-decoration: none;
}
