.project{
    font-size: calc(10px + 2vmin);
}

.header{
    border: none;
    padding: .75rem 1.25rem;
    background: none;
}

.header-text{
    border: none;
    cursor: pointer;
    color: #ffcb9a;
    background: none;
    outline: none;
    transition: color 0.2s;
}

.header-text:focus{
    outline: none;
}

.header-text:hover{
    position: relative;
    color: rgba(255, 254, 198, 0.973);
}

.card-body{
    border-top: solid;
    border-bottom: solid;
    border-color: white;
    font-size: 20px;
}

.project-links{
    display: flex;
    justify-content: center;
    margin-top: 1%;
    margin-bottom: 2%;
}

.link{
    color: #7395ae;
    margin: 0 1% 0 1%;
    transition: color .2s;
}

.link:hover{
    text-decoration: none;
    color: #e8a87c;
}

.previewImage{
    width: 30%;
    height: 30vh;
}

.project-preview{
    display: flex;
    justify-content: center;
    margin-top: 1%;
    margin-bottom: 2%;
}
