
.projects{
    color: white;
    background: #5d5c61;
    height: 100%;
    padding-bottom: 10%;
}

.projects>h1{
    padding-top: 5%;
    font-family: Georgia;
}

.project-names{
    padding: 4% 10% 4% 10%;
}

.arrowup{
    border: none;
    background: none;
    color: white;
    transition: color 0.2s;
    position: relative;
    margin: 10px 0 0 0;
}

.arrowup:hover{
    color: black;
    text-decoration: none;
}
